.math-question {
	margin: 2em 3vw;
	padding: 1em 2em;
	background: white;
	box-shadow: 0 0 1.5em #546e7a;
	border-radius: 2em;
}

.katex {
	overflow: auto;
}

.math-q .katex {
	padding: 0 !important;
	height: fit-content;
}

.math-a .katex {
	display: unset !important;
}

.math-a-col {
	border: 0.15em solid transparent;
	color: #455a64;
	background: rgba(207, 216, 220, 0.5);
	border-radius: 2em;
	margin: 0.5em;
	cursor: pointer;
	transition: 250ms;
	z-index: 1;
}

.locked {
	cursor: auto;
}

.selected {
	border: 0.15em solid #3f51b5;
	color: #303f9f;
	background: rgba(121, 134, 203, 0.5);
}

.selected-correct {
	border: 0.15em solid transparent;
	color: white;
	background: #00c853;
}

.unselected-correct {
	border: 0.15em solid transparent;
	color: white;
	background: #d50000;
}

.source-col {
	padding-right: 2em;
	color: #b0bec5;
	text-align: right;
	font-style: italic;
	z-index: 1;
}

@media (hover: hover) {
	.math-question {
		position: relative;
		overflow: hidden;
	}

	.question-number {
		position: absolute;
		bottom: 0;
		color: #fff8e1;
		font-size: 25em;
		font-style: italic;
		line-height: 0.8;
	}
	.question-number-left {
		left: 30px;
	}
	.question-number-right {
		right: 30px;
	}

	.math-q-row {
		pointer-events: none;
	}

	.math-q .katex {
		overflow: visible;
	}

	.math-q ::-webkit-scrollbar {
		display: none;
	}

	.math-a-col:not(.selected):not(.locked):hover {
		color: #827717;
		background: rgba(255, 224, 130, 0.5);
		box-shadow: 0 0 1em rgba(130, 119, 23, 0.5);
	}
}

@media (hover: none) {
	.question-number {
		display: none;
	}
}
